import React,{Component} from 'react' ;
import{MenuComp} from '../components.js' ;

export default class HomeComp extends Component {
    /* PropTypes */
    /*static propTypes = {
        // name
        name: React.PropTypes.string.isRequired
    };
*/

    /*
     * props 
     * getDefaultProps 
     * */
/*
    static defaultProps = {
        items: [
            {item: '1', content: '1'},
            {item: '2', content: '2'},
            {item: '3', content: '3'},
            {item: '4', content: '4'},
            {item: '5', content: '5'},
            {item: '6', content: '6'},
            {item: '7', content: '7'}
        ],
        name: ''
    } ;

*/

    /*
     * this.state  this.setState()  state 
     * getInitialState()  state 
     * */
    constructor(props){
        super(props);
        this.state = {
            name: 'World!'
        };
    }

    /*  */
    render() {
        let monthNodes = this.props.items.map(function (kitem) {
            return (
                <li key={kitem.item}>{kitem.item}:{kitem.content}</li>
            );
        });
        return (
            <div>
                <MenuComp />
                <div className='content-wrap'>
                    <h1 className='p'>Hello {this.state.name}</h1>
                    <p ref='myNode'></p>
                    <ul>
                        {monthNodes}
                    </ul>
                </div>
            </div>
        );
    }

    /* domdom */
    componentDidMount() {

    }
}
HomeComp.defaultProps = {
    items: [
        {item: '1', content: '1'},
        {item: '2', content: '2'},
        {item: '3', content: '3'},
        {item: '4', content: '4'},
        {item: '5', content: '5'},
        {item: '6', content: '6'},
        {item: '7', content: '7'}
    ],
    name: ''
} ;




